<template>
    <section class="section">
        <div v-bind:id="chart_id" style='height:580px'></div>
    </section>
</template>


<script>
import Vue from 'vue'
import {initKOption} from '../assets/kLine.js'
import conf from '../assets/config.js'
import axios from 'axios'

// 引入echarts
import echarts from 'echarts'
//一般都要加个$加到vue的原型链上，方便引用
Vue.prototype.$echarts = echarts 

export default {
    props: {
        freq: String,  // K线级别，可选值 1min、5min、30min、D、W
        ts_code: String,
        trade_date: String,
        asset: String,
    },

    data () {
        return {
            loading: true,
            status: "",
        }
    },

    computed: {
        chart_id () {
            return "chart-" + this.freq
        },
        params () {
            return {
                ts_code: this.ts_code,
                freq: this.freq,
                asset: this.asset,
                trade_date: this.trade_date
            }
        }
    },

    mounted () {
        this.drawKline()
        // if (this.timer) {
        //     clearInterval(this.timer)
        // } else {
        //     var interval = 60*30
        //     if (this.isToday(this.trade_date)) {
        //         if (this.freq === '1min') {
        //             interval = 60
        //         } else if (this.freq === '5min') {
        //             interval = 150
        //         } else if (this.freq === '30min') {
        //             interval = 60*15
        //         } else if (this.freq === 'D') {
        //             interval = 60*30
        //         } else {
        //             interval = 60
        //         }
        //     }
        //     // eslint-disable-next-line
        //     console.log('freq: %s, interval: %s', this.freq, interval.toString())
        //     this.timer = setInterval( () => {
        //         this.drawKline()
        //     }, interval*1000)
        // }
        // this.drawKline()
    },

    destroyed () {
        clearInterval(this.timer)
    },

    watch: {
        params: function (val, oldVal) {
            // eslint-disable-next-line
            console.log('new: %s, old: %s', val.trade_date, oldVal.trade_date)
            this.drawKline()
        }
    },

    methods:{
        drawKline () {
            this.loading = true; 
            const url = conf.api + "/kline?ts_code=" + this.ts_code + "&freq=" + this.freq + "&trade_date=" + this.trade_date + "&asset=" + this.asset

            var kChart = this.$echarts.init(document.getElementById(this.chart_id));
            kChart.showLoading({
                text: "正在加载 " + this.ts_code + " - " + this.freq + " - " + this.trade_date,
                color: '#c23531',
                textColor: 'red',
                maskColor: 'rgba(255, 255, 255, 0.6)',
                zlevel: 0
            })

            axios.get(url).then((response) => {
                var data = response.data
                this.status = data.status
                // eslint-disable-next-line
                // console.log(data)
                return data.kdata
            }).then((kdata) => {
                // 清空
                // var oldChart = this.$echarts.init(document.getElementById(this.chart_id));
                // oldChart.dispose();
                const title = {
                    text: this.ts_code + " - " + this.freq, 
                    textStyle: {
                        color: "red",
                        fontSize: 24
                    }
                }

                var option = initKOption(kdata, title);
                kChart.setOption(option); 
                // kChart.onresize = kChart.resize;
                kChart.hideLoading()
                kChart.onresize = function() {
                    kChart.resize();
                    //myChart1.resize();    //若有多个图表变动，可多写
                }
            }).catch((error) => {
                // eslint-disable-next-line
                console.log("获取 K 线失败: " + error)
            });
        },
        isToday(str) {
            // example: str = 20200210
            var d = str.slice(0, 4) + "/" + str.slice(4, 6) + "/" + str.slice(6, 8)
            if (new Date(d).toDateString()==new Date().toDateString()){
                return true
            } else {
                return false
            }
        }, 
    },

}
</script>

<style scoped>
.status {
    font-size: 18;
    font-family: Arial, Helvetica, sans-serif;
    color:red
}

.block {
    margin:0px 20px;
}

.date-picker {
    font-size: 18;
    font-family: Arial, Helvetica, sans-serif;
    color: brown
}
</style>
