<template>
    <div>
        <div class="tile is-ancestor">
        <div class="tile is-parent">
            <el-table :data="basic" stripe style="width: 100%">
                <el-table-column prop="symbol" label="交易代码" width="180" align='center'></el-table-column>
                <el-table-column prop="name" label="股票名称" width="180" align='center'></el-table-column>
                <el-table-column prop="area" label="区域" width="180" align='center'></el-table-column>
                <el-table-column prop="industry" label="行业" width="180" align='center'></el-table-column>
                <el-table-column prop="market" label="市场类型" width="180" align='center'></el-table-column>
                <el-table-column prop="list_date" label="上市日期" width="180" align='center'></el-table-column>        
            </el-table>
        </div>
        <div class="tile is-parent is-4">
            <el-link :href=f10_url target="_blank">同花顺F10</el-link>
        </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios'
import conf from '../assets/config.js'

export default {
    props: {
        ts_code: String,
    },

    data () {
        return {
            basic: [{
                symbol: "",
                name: "",
                area: "",
                industry: "",
                market: "",
                list_date: ""
            }],
            signals: [],
            dialogTableVisible: false,
        }
    },

    computed: {
        f10_url () {
            return `http://basic.10jqka.com.cn/${this.ts_code.substring(0, 6)}`
        }
    },

    mounted () {
        this.getBasic()
        // this.getSignals()
    },

    watch: {
        ts_code: function (val, oldVal) {
            // eslint-disable-next-line
            console.log('new: %s, old: %s', val.end_date, oldVal.end_date)
            this.getBasic()
        }
    },

    methods: {
        getBasic () {
            this.loading = true; 
            const url = conf.api + "/basic?ts_code=" + this.ts_code
            // const url = "http://localhost:8000/basic?ts_code=" + this.ts_code
            
            axios.get(url).then((response) => {
                var data = response.data
                // eslint-disable-next-line
                console.log(data)
                this.basic = data.basic
            }).then(() => {
                this.loading = false;
            }).catch((error) => {
                // eslint-disable-next-line
                console.log("获取股票基础信息失败: " + error)
                this.loading = false;
            });
        },

        // getSignals () {
        //     const url = conf.api + "/signals?ts_code=" + this.ts_code
        //     axios.get(url).then((response) => {
        //         var data = response.data
        //         this.signals = data['买入信号'].concat(data['卖出信号'])
        //         // eslint-disable-next-line
        //         console.log(this.signals)
        //     })
        // },
    }
}
</script>