    <br>
    <!-- 参数表单 -->
    <div class="k-forms">
      <el-form :inline="true" class="form-inline">
        <el-form-item label="参数配置: ">
          <el-tooltip class="item" effect="dark" content="请输入标的代码" placement="top-start">
            <el-input v-model="ts_code_new" placeholder="请输入标的代码"></el-input>
          </el-tooltip>
        </el-form-item>

        <el-form-item>
          <el-tooltip class="item" effect="dark" content="请选择资产类型" placement="top-start">
            <el-select v-model="asset_new" placeholder="资产类型">
              <div v-for="asset in asset_types" :key="asset">
                <el-option :label=asset :value=asset></el-option>
              </div>
            </el-select>
          </el-tooltip>
        </el-form-item>

        <el-form-item label="K线级别: ">
          <el-tooltip class="item" effect="dark" content="请选择需要展示的多个级别 K 线" placement="top-start">
