        <el-form-item>
            <span class="date-picker">请选择交易日期： </span>
            <el-date-picker
            v-model="trade_date_new"
            align="right"
            type="date"
            placeholder="选择日期"
            value-format="yyyyMMdd"
            v-loading="loading"
            :picker-options="pickerOptions">
            </el-date-picker>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" @click="updatePara">更新参数</el-button>
        </el-form-item>
      </el-form>
