    <Basic v-if="asset === 'E'" v-bind:ts_code="ts_code"></Basic> 
    <!-- 不同级别的K线分析结果 -->
    <div v-for="level in freqs.split(',')" :key="level">
      <plot-kline v-bind:freq="level" v-bind:ts_code="ts_code" v-bind:asset="asset" v-bind:trade_date="trade_date"></plot-kline>
