<script>
import Footer from '../components/Footer.vue';
import PlotKline from '../components/PlotKline.vue';
import Basic from '../components/Basic.vue';

import Vue from 'vue'
import Buefy from 'buefy'
import 'buefy/dist/buefy.css'

Vue.use(Buefy)

export default {
  name: 'kline',
  props: {
    ts_code: {
      type: String,
      default: '000001.SH'
    },
    asset: {
      type: String,
      default: 'I'
    },
    trade_date: {
      type: String,
      default: '20200222'
    },
    freqs: {
      type: String,
      default: '5min,30min'
    },
  },

  data () {
      return {
        asset_types: ['E', 'I'],
        levels: ['1min', '5min', '30min', '60min', 'D', 'W'],
        ts_code_new: this.ts_code,
        asset_new: this.asset,
        levels_checked: this.freqs.split(","),
        trade_date_new: this.trade_date
      }
  },

  components: {
    Footer,
    PlotKline,
    Basic,
  },

  mounted() {
    document.title = `缠论分析(${this.ts_code_new}|${this.trade_date_new})`
  },
  
  methods: {
    updatePara () {
      this.$router.push("/?ts_code=" + this.ts_code_new + "&asset=" + this.asset_new + "&trade_date=" + this.trade_date_new + "&freqs=" + this.levels_checked.join(","))
    },
  }
}
</script>

