
import Vue from "vue";
import Router from "vue-router";

import Kline from '../pages/Kline.vue'

Vue.use(Router)

var router = [
    // {path: "/", name: "index", redirect: '/kline'},
    {
        path: "/", 
        name: "kline", 
        component: Kline,
        meta: { title: 'K线' },
        props:  (route) => ({ 
            ts_code: route.query.ts_code,  
            asset: route.query.asset,  
            trade_date: route.query.trade_date,
            freqs: route.query.freqs
        }),
    }
]

export default new Router({
    mode: 'history',
    routes: router
})


